$font-color: $black;


.menu {
  background-color: $white;
  @include reset-list-style;

  @include tablet {
    background-color: transparent;
    font-size: rem(15);
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    width: 50%;
  }

  @include desktop {
    align-items: center;
    background-color: $white;
    max-width: rem(410);
    font-size: rem(17);
  }

  &__item {
    text-align: center;
    padding-top: rem(28);
    padding-bottom: rem(28);
    border-bottom: 1px solid rgba($black, 0.1);

    @include tablet {
      padding: 0;
      text-align: left;
      border-bottom: none;
      width: 50%;
    }
  }

}
